html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
}

h1 {
  margin: 0.5em 0;
  font-size: 2rem;
}
h2 {
  padding-bottom: 5px;
  font-size: 1.5rem;
}

h3 {
  margin: 0.5em 0;
  font-size: 1.2rem;
}

a {
  text-decoration: none;
}

.header {
  display: flex;
  justify-content: space-between;
  background-color: #e0e0e0;
  padding: 1rem;
  margin: 0.5rem;
  border-radius: 0.5rem;
  align-items: center;
}

.content {
  display: flex;
  justify-content: start;
}

.maincontent {
  background-color: #e0e0e0;
  padding: 1rem;
  margin: 0.5rem;
  border-radius: 0.5rem;
}

.img {
  max-height: 15rem;
}

.images {
  margin-right: 50px;
  padding-top: 20px;
}

.commodity {
  display: flex;
}

button {
  width: 100%;
  padding: 0.2rem;
  background-color: rgba(7, 110, 71, 0.329);
  align-items: center;
  border-radius: 0.5rem;
}

button.add {
  background-color: #40c0f0;
  width: 1.5rem;
}

button.remove {
  background-color: #f04040;
  width: 1.5rem;
}

button.badge {
  background-color: #f04040;
  color: #ffffff;
  width: 1.5rem;
}

.checkoutcontent {
  width: 100%;
  background-color: #e0e0e0;
  padding: 1rem;
  margin: 0.5rem;
  border-radius: 0.5rem;
}

.row {
  display: flex;
  justify-content: space-between;
}

.checkoutMain {
  width: 30%;
}

.checkout {
  width: 40%;
}

.text-right {
  text-align: right;
}
